<script lang="ts">
  import { stickersURL } from "../store";
  import Siema from "siema";
  import { onMount, onDestroy } from "svelte";

  let siema;
  let sticker;
  let arrayStickers = Array(7);

  onMount(() => {
    siema = new Siema({
      selector: siema,
      perPage: { 100: 3, 600: 6 },
    });
  });

  onDestroy(() => {
    siema.destroy();
  });

  function getSticker(e) {
    // $stickersURL = e.target.src;
    stickersURL.update((v) => {
      return [...v, e.target.src];
    });
  }
</script>

<div class="flex my-2 py-2">
  <button class="mx-2 px-2" on:click={siema.prev()}>
    <i class="fas fa-arrow-left" />
  </button>
  <div bind:this={siema} class="w-full" on:click={(e) => getSticker(e)}>
    {#each arrayStickers as item, i}
      <div class="w-16 ">
        <img src="img/{++i}.svg" alt="" class="cursor-pointer" />
      </div>
    {/each}
  </div>
  <button class="mx-2 px-2" on:click={siema.next()}>
    <i class="fas fa-arrow-right" />
  </button>
</div>
