<script lang="ts">
  import { photo, cropperInstance, photoFilter } from "../store";
  import Cropper from "cropperjs";
  import { onMount, onDestroy } from "svelte";
  let cropper;
  let thisImages;
  onMount(async () => {
    cropper = new Cropper(thisImages, {
      autoCropArea: 1,
      autoCrop: true,
      cropend() {
        photo.update((v) => {
          let newURL = cropper.getCroppedCanvas().toDataURL();
          return { ...v, newPhoto: newURL };
        });
      },
    });
    $cropperInstance = cropper;
  });
  onDestroy(async () => {
    cropper.destroy();
    cropper = "";
    $cropperInstance = undefined;
  });
</script>

<div class="flex justify-center items-center h-full ">
  <div>
    <img class="max-h-397" src={$photo.photo} alt="" bind:this={thisImages} />
  </div>
</div>
