<script lang="ts">
  import Siema from "siema";
  import { onMount } from "svelte";
  import { photoFilter } from "../store";

  let siema;
  let filtesrs: any[] = [
    "none",
    "_1977",
    "aden",
    "brannan",
    "brooklyn",
    "clarendon",
    "earlybird",
    "gingham",
    "hudson",
    "inkwell",
    "kelvin",
    "lark",
    "lofi",
    "maven",
    "mayfair",
    "moon",
    "nashville",
    "perpetua",
    "reyes",
    "rise",
    "slumber",
    "stinson",
    "toaster",
    "valencia",
    "walden",
    "willow",
    "xpro2",
  ];

  onMount(() => {
    siema = new Siema({
      selector: siema,
      perPage: { 100: 3, 600: 6 },
    });
    return () => {
      siema.destroy();
    };
  });

  function setFilter(e) {
    $photoFilter = e.target.dataset.filter;
  }
</script>

<div class="flex my-2">
  <button class="mx-2 px-2" on:click={siema.prev()}>
    <i class="fas fa-arrow-left" />
  </button>
  <div bind:this={siema} class="w-full" on:click={(e) => setFilter(e)}>
    {#each filtesrs as item}
      <div class="mx-1 cursor-pointer">
        <img
          src="img/cover.png"
          class="{item} w-16 object-cover"
          data-filter={item}
          alt="" />
        <p class="text-center text-10 font-bold text-gray-600 w-16">{item}</p>
      </div>
    {/each}
  </div>
  <button class="mx-2 px-2" on:click={siema.next()}>
    <i class="fas fa-arrow-right" />
  </button>
</div>
