<script lang="ts">
  import { cropperInstance } from "../store";
  import { photo } from "../store";
  let value;
  let coordinats = { w: undefined, h: undefined };
  // @ts-ignore
  $: startCrop(coordinats, value);
  function startCrop() {
    switch (value) {
      case "1/1":
        $cropperInstance.setAspectRatio(1);
        break;
      case "free":
        $cropperInstance.setAspectRatio(NaN);
        break;
      case "nofree":
        $cropperInstance.setAspectRatio(NaN);
        $cropperInstance.setData({
          width: coordinats.w,
          height: coordinats.h,
        });
        break;
      default:
        break;
    }
  }
</script>

<div class="text-xs py-2 space-y-2">
  <label class=" flex items-center space-x-2">
    <input
      bind:group={value}
      type="radio"
      name="crop"
      value="free"
      class="align-middle inline-block" />
    <span>Произвольный размер</span>
  </label>
  <label class="flex items-center space-x-2">
    <input bind:group={value} type="radio" name="crop" value="1/1" />
    <span>Квадрат</span>
  </label>
  <div class="flex items-center space-x-2">
    <label class="flex items-center space-x-2">
      <input bind:group={value} type="radio" name="crop" value="nofree" />
      <span>Точный размер</span>
    </label>
    <input
      bind:value={coordinats.w}
      type="number"
      min="300"
      class="bg-gray-200 pl-2 w-24 h-5"
      placeholder="Ширина" />
    <input
      bind:value={coordinats.h}
      type="number"
      min="300"
      class="bg-gray-200 pl-2 w-24 h-5"
      placeholder="Высота" />
  </div>
</div>
