<script>
  import { createEventDispatcher } from "svelte";
  const dispatch = createEventDispatcher();
  export let button;
  export let activeButton;

  function sendNameComponent() {
    dispatch("nameComponent", button.name);
  }
</script>

<li
  class="flex flex-col justify-center items-center group"
  on:click={sendNameComponent}>
  <span
    class="py-2 px-2 {button.name === activeButton ? 'bg-blue-600' : 'bg-gray-500'}
    rounded-lg group-hover:bg-blue-600 transition-colors duration-200
    cursor-pointer">
    <i
      class="{button.icon} text-base text-gray-100 group-hover:text-gray-500" />
  </span>
  <span class="pt-1 text-gray-800 cursor-default">{button.title}</span>
</li>
