<script lang="ts">
  import { photo } from "../store";
  let error;

  function addFile(e) {
    if (!e.target.files[0].type.startsWith("image/")) {
      error = true;
      return;
    }
    let reader = new FileReader();
    reader.onload = () => {
      return photo.update((v) => {
        return { ...v, photo: reader.result, newPhoto: reader.result };
      });
    };
    reader.readAsDataURL(e.target.files[0]);
  }

  let inputBind = null; // реф на инпут файла
  // обработчик клика по кнопке добавления фото
  function clickButton() {
    inputBind.click();
  }
</script>

<div class="flex flex-col justify-center items-center h-full">
  <input
    class="hidden"
    type="file"
    on:change={addFile}
    bind:this={inputBind}
    accept="image/*" />

  <button
    class="w-10 h-10 bg-blue-400 rounded-full focus:outline-none text-2xl
    text-white"
    on:click={clickButton}>
    +
  </button>
  {#if error}
    <p class="text-2xl text-red-400 text-center font-bold">Только фото</p>
  {/if}
</div>
