<script lang="ts">
  import { image, photoFilter, stickersURL, enterText } from "../store";
  import { onMount } from "svelte";
  import interact from "interactjs";

  onMount(() => {
    interact(".target")
      .draggable({
        inertia: true,
        onmove(event) {
          let x =
            (parseFloat(event.target.getAttribute("data-x")) || 0) + event.dx;
          let y =
            (parseFloat(event.target.getAttribute("data-y")) || 0) + event.dy;

          let angle = event.target.getAttribute("data-angle") || 0;
          event.target.style.transform = `translate(${x}px, ${y}px) rotate(${angle}rad)`;
          event.target.setAttribute("data-x", x);
          event.target.setAttribute("data-y", y);
        },
      })
      .resizable({
        edges: { right: ".resizer", bottom: ".resizer" },
        invert: "none",
        onmove(event) {
          event.target.style.width = event.rect.width + "px";
          event.target.style.height = event.rect.height + "px";
        },
      });

    ///
    interact(".rotation-handle").draggable({
      onstart: function (event) {
        let box = event.target.parentElement;
        let rect = box.getBoundingClientRect();

        // store the center as the element has css `transform-origin: center center`
        box.setAttribute("data-center-x", rect.left + rect.width / 2);
        box.setAttribute("data-center-y", rect.top + rect.height / 2);
        // get the angle of the element when the drag starts
        box.setAttribute("data-angle", getDragAngle(event));
      },
      onmove: function (event) {
        let box = event.target.parentElement;

        let pos = {
          x: parseFloat(box.getAttribute("data-x")) || 0,
          y: parseFloat(box.getAttribute("data-y")) || 0,
        };

        let angle = getDragAngle(event);
        // update transform style on dragmove
        box.style.transform = `translate(${pos.x}px, ${pos.y}px) rotate(${angle}rad)`;
      },
      onend: function (event) {
        let box = event.target.parentElement;

        // save the angle on dragend
        box.setAttribute("data-angle", getDragAngle(event));
      },
    });

    function getDragAngle(event) {
      let box = event.target.parentElement;
      let startAngle = parseFloat(box.getAttribute("data-angle")) || 0;
      let center = {
        x: parseFloat(box.getAttribute("data-center-x")) || 0,
        y: parseFloat(box.getAttribute("data-center-y")) || 0,
      };
      let angle = Math.atan2(
        center.y - event.clientY,
        center.x - event.clientX
      );

      return angle - startAngle;
    }

    ///

    return () => {
      interact(".target").unset();
      interact(".rotation-handle").unset();
      $stickersURL = [];
      $enterText = { text: "", color: "" };
    };
  });

  function removeSticker(e, index) {
    $stickersURL[index] = undefined;
    if ($stickersURL.every((i) => i === undefined)) $stickersURL = [];
  }
</script>

<div class="flex justify-center items-center min-h-full">
  <div
    id="main"
    class="overflow-hidden relative h-auto w-auto flex justify-center
    items-center">
    <img class="max-h-397 {$photoFilter}" src={$image} alt="" />

    {#if $stickersURL}
      {#each $stickersURL as item, i}
        {#if item}
          <div
            class="absolute w-20 h-20 hover:border-gray-400 hover:border border
            border-transparent target group"
            style="touch-action: none; user-select: none;">
            <img src={item} alt="" class="w-full h-full" />
            <div
              class="w-3 h-3 rounded-full absolute group-hover:block
              group-hover:bg-white rotation-handle"
              style="top:-5px; right: -5px" />
            <div
              class="w-3 h-3 absolute resizer group-hover:block
              group-hover:bg-white"
              style="right: -5px; bottom: -5px " />
            <div
              class="w-3 h-3 absolute group-hover:block group-hover:bg-red-500"
              style="top: -5px; left: -5px "
              on:click={(e) => removeSticker(e, i)} />
          </div>
        {/if}
      {/each}
    {/if}
    {#if $enterText.text.length > 0}
      <div
        class="absolute hover:border-gray-400 hover:border border
        border-transparent target group"
        style="touch-action: none; user-select: none;">
        <div
          style="color:{$enterText.color}; font-size:20px; white-space:
          pre-line; line-height: 1"
          class="font-bold">
          {$enterText.text}
        </div>
        <div
          class="w-3 h-3 rounded-full absolute group-hover:block
          group-hover:bg-white rotation-handle"
          style="top:-5px; right: -5px" />
        <!-- <div
          class="w-3 h-3 absolute resizer group-hover:block group-hover:bg-white"
          style="right: -5px; bottom: -5px " /> -->
        <div
          class="w-3 h-3 absolute group-hover:block group-hover:bg-red-500"
          style="top: -5px; left: -5px "
          on:click={() => ($enterText = { text: '', color: '' })} />
      </div>
    {/if}
  </div>
</div>
