<script lang="ts">
  import { slide } from "svelte/transition";
  import { photo, enterText, photoFilter, image } from "./store";
  import NewButtonMain from "./components/NewButtonMain.svelte";
  import RawImage from "./components/RawImage.svelte";
  import ButtonAddFile from "./components/ButtonAddFile.svelte";
  import Cropper from "./components/Cropper.svelte";
  import Sticker from "./components/Sticker.svelte";
  import ParamCrop from "./components/ParamCrop.svelte";
  import ParamFilter from "./components/ParamFilter.svelte";
  import ParamSticker from "./components/ParamSticker.svelte";
  import ParamText from "./components/ParamText.svelte";
  import ParamOptions from "./components/ParamOptions.svelte";
  ///
  import html2canvas from "html2canvas";
  ///
  let component = RawImage;
  let componentParam;
  let activeButton: any = "";
  let savePhoto = false;

  // данные для главного меню слева
  let buttons: any[] = [
    { name: "crop", icon: "fas fa-crop-alt", title: "Кадрировать" },
    //{ name: "filter", icon: "fas fa-fire-alt", title: "Фильтр" },
    { name: "sticker", icon: "far fa-smile", title: "Стикеры" },
    { name: "text", icon: "fas fa-text-height", title: "Текст" },
    //{ name: "param", icon: "fas fa-cubes", title: "Настройки" },
  ];

  // имя компонента
  function nameComponent(e) {
    activeButton = e.detail;
    switch (e.detail) {
      case "crop":
        component = Cropper;
        componentParam = ParamCrop;
        break;
      case "filter":
        component = Sticker;
        componentParam = ParamFilter;
        break;
      case "sticker":
        component = Sticker;
        componentParam = ParamSticker;
        break;
      case "text":
        component = Sticker;
        componentParam = ParamText;
        break;
      case "param":
        component = Sticker;
        componentParam = ParamOptions;
        break;
      default:
        component = RawImage;
        componentParam = undefined;
        break;
    }
  }

  function resetComponents() {
    component = RawImage;
    activeButton = "";
    $photo.newPhoto = undefined;
  }

  function thisIsEnd() {
    if (component === Cropper) {
      component = RawImage;
      return;
    }
    savePhoto = true;
    let a = document.getElementById("main");
    let s = a.getElementsByTagName("img");
    let { naturalWidth, naturalHeight } = s[0];
    s[0].style.opacity = "0";
    let canvas = document.createElement("canvas");
    canvas.width = naturalWidth;
    canvas.height = naturalHeight;
    let context = canvas.getContext("2d");
    html2canvas(a, {
      backgroundColor: null,
    })
      .then((v) => {
        let img = new Image();
        img.src = v.toDataURL();
        return { img };
      })
      .then(({ img }) => {
        let mainPhoto = new Image(naturalWidth, naturalHeight);
        mainPhoto.src = $photo.newPhoto;
        return { img, mainPhoto };
      })
      .then(({ img, mainPhoto }) => {
        context.drawImage(mainPhoto, 0, 0);
        context.drawImage(img, 0, 0, naturalWidth, naturalHeight);
        $photo.newPhoto = canvas.toDataURL();
        return { img, mainPhoto };
      })
      .then(({ img, mainPhoto }) => {
        canvas.remove();
        img.remove();
        mainPhoto.remove();
        component = RawImage;
        savePhoto = false;
      });
  }
</script>

<svelte:head>
  <link
    rel="stylesheet"
    href="https://cdnjs.cloudflare.com/ajax/libs/cssgram/0.1.10/cssgram.min.css" />
  <script
    src="https://kit.fontawesome.com/222c28344d.js"
    crossorigin="anonymous">

  </script>
</svelte:head>

<div class="flex justify-center items-center bg-blue-500 h-full">
  <div class="w-660 h-550 bg-white py-2 px-2 rounded-lg overflow-hidden">
    {#if $photo.photo === undefined}
      <ButtonAddFile />
    {:else}
      <div class="flex flex-col h-full">
        <div class="flex-grow ">
          <div class="h-full flex ">
            <div class="FOTO_COMPONENT flex-grow bg-gray-800">
              {#if savePhoto}
                <div class="flex justify-center items-center h-full text-white">
                  Клею пикселы
                </div>
              {:else}
                <svelte:component this={component} />
              {/if}
            </div>
            <div class="MAIN_NAV">
              <ul
                class="flex flex-col justify-around items-center h-full text-10
                px-2">
                {#each buttons as button}
                  <NewButtonMain
                    {button}
                    {activeButton}
                    on:nameComponent={nameComponent} />
                {/each}
              </ul>
            </div>
          </div>
        </div>
        {#if component !== RawImage && !savePhoto}
          <div class="PARAM_COMPONENT h-88 overflow-hidden" transition:slide>
            {#each [0] as _ (componentParam)}
              <div transition:slide>
                <svelte:component this={componentParam} />
              </div>
            {/each}
          </div>
          {#if !savePhoto}
            <div class="space-x-2 text-sm" transition:slide>
              <button
                on:click={thisIsEnd}
                class="px-4 py-2 rounded-md bg-blue-600 text-white border-2
                border-blue-600 hover:bg-blue-800 transition duration-500
                focus:outline-none">
                Применить
              </button>
              <button
                on:click={resetComponents}
                class="px-4 py-2 mt-2 rounded-md border-2 border-blue-600
                text-blue-600 hover:text-white hover:bg-blue-600 transition
                duration-500 focus:outline-none">
                Отменить
              </button>
            </div>
          {/if}
        {/if}
      </div>
    {/if}
  </div>
</div>
